define('entities/questions',['require','backbone','msgbus'],function (require) {

    /*jshint camelcase: false */
    'use strict';

    //var app = require('app');
    var Backbone = require('backbone');
    var msgBus = require('msgbus');
    var Entities = {};

    Entities.Question = Backbone.Model.extend({
        idAttribute: 'question_id',
        defaults: {
            tags: null,
            owner: null,
            is_answered: null,
            view_count: null,
            accepted_answer_id: null,
            answer_count: null,
            score: null,
            last_activity_date: null,
            creation_date: null,
            last_edit_date: null,
            link: null,
            title: null
        }
    });

    Entities.QuestionCollection = Backbone.Collection.extend({
        model: Entities.Question,
        parse : function(response){
            return response.items;  
        }, 
        url: function () {
            return 'https://api.stackexchange.com/2.2/questions?order=desc&sort=activity&site=stackoverflow';
        }
    });

    var API = {

        getQuestionsEntities: function () {
            var collection = new Entities.QuestionCollection();
            var defer = $.Deferred();
            // show loading view  while fetching data
            msgBus.trigger('loading:show', {message: 'Loading...'});

                collection.fetch({
                    success: function (data) {
                        defer.resolve(data);
                        msgBus.trigger('loading:hide');
                    },
                    error: function (model, jqXHR, textStatus) {
                        msgBus.trigger('loading:hide');
                        defer.reject(model, jqXHR, textStatus);
                    }
                });

            return defer.promise();
        }

    };

    msgBus.commands.setHandler('questions:entities', function () {
        return API.getQuestionsEntities();
    });


});

